.App {
  text-align: center;
  background-color: rgb(225, 168, 197);
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.App {
  text-align: center;
  padding: 20px;
}

.photo-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.photo-card {
  border: 1px solid #e9a3dd;
  border-radius: 5px;
  margin: 10px;
  padding: 10px;
  width: 300px;
  background-color: #fff;
}

.photo-card img {
  max-width: 100%;
}

.filter {
  margin-bottom: 20px;
}

.filter input {
  margin-right: 10px;
}

.btn_search{
  appearance: none;
	border: .2em solid #E91E63;
	background: hsl(0 0 0/0);
	padding: .85em 1.5em;
	color: #E91E63;
	border-radius: 2em;
	transition: 1s;

}

.btn_search:hover {
  background: #E91E63;
		color: #fff;
}

input[type=date] {
	border: .2em solid #E91E63;
	padding: .8em .9em;
	border-radius: 2em;
	color: #E91E63;
}